import axios from "axios";
//store
import store from "../store/store"

let isRush = false;

// 
axios.interceptors.request.use(
        confing => {
            // vuextoken
            // httpheadertokentoken
            // tokentoken
            //
            const token = localStorage.getItem("isSeesion");
            const userId = localStorage.getItem("userId");
            const nowDate = localStorage.getItem("nowDate");
            const MIN15 = 15 * 60 * 1000
            if (Date.now() > parseInt(nowDate) + MIN15 && token) {
                if (!isRush) {
                    isRush = true
                    return new Promise(function(resolve, reject) {
                        //token
                        let loginParmas = {
                            mobile: localStorage.getItem("mobile"),
                            password: localStorage.getItem("password"),
                        };
                        axios.post('/api/login/login', loginParmas).then(response => {
                            //
                            // console.log("=================================")
                            //15
                            localStorage.setItem("nowDate", Date.now());
                            localStorage.setItem("isSeesion", response.data.JSESSIONID2);
                            confing.headers = {
                                'token': token,
                                "userId": userId
                            }
                            resolve(confing);
                        }).finally(() => {
                            isRush = false
                        })
                    });
                }
                return confing
            } else {
                // console.log("=================================")
                if (token != '' && token) {
                    confing.headers = {
                        'token': token,
                        "userId": userId
                    }
                }
                return confing;
            }
        },
        (error) => {
            return Promise.reject(error)
        }
    )
    //

// 
axios.interceptors.response.use(
    response => {
        // 200
        // 
        if (response.status === 200) {
            // console.log(response)
            return response;
        } else {
            return Promise.reject(response);
        }
    },
    error => {
        return Promise.reject(error.response);
    })
export default axios


