

import miHeader from "../common/header";


import until from "@/until/until";

import XTable from 'vux/src/components/x-table/index.vue'


import UserInfo from "../Login/loginCommon";

import { mapState, mapActions } from "vuex";
export default {
components: {
miHeader,
XTable,
},
data() {
return {
title: "",
tableList: [],
};
},
created() {
this.helloWord();
},
mounted() {},
computed: {
...mapState(["wechartWH"]),
},
methods: {

...mapActions(["getSession", "GetUserInfo"]),
helloWord() {
let params = {
page: 1,
rows: 10,
};
let that = this;
this.axios.post("/api/aiCustomer/callList", params).then(function (res) {
console.log(res.rows);
that.tableList = res.data.rows;
});
},
},
};



