value6: [""],
list: [["", ""]],
itmeSelect: {
MissingRemind: "2",
CustomAddress: false,
CustomScenarios: "1",
},
CustomScenariosList: ["", "", ""],
gender: "",
isHelp: true,
};
},
created() {},
computed: {
...mapState(["isShowFooter"]),
},
methods: {
...mapMutations(["handleChangeFooter"]),
onItemClick(param) {
this.detailMsg = param;
},
helloWord() {
let self = this;

console.log(this.wechartWH);
this.axios
.post("/api/aiCustomer/callList", { page: 1, rows: 10 })
.then(function (res) {
self.tableList = res.data.rows;
console.log(self.tableList);
});
},
handleCustom(newVal, oldVal) {
this.itmeSelect.CustomAddress = !this.itmeSelect.CustomAddress;
},
handleHelp() {
this.isHelp = !this.isHelp;
this.$store.commit("handleChangeFooter", this.isHelp);
},
handelback() {
this.isHelp = !this.isHelp;
this.$store.commit("handleChangeFooter", this.isHelp);
},
},
};



