

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import router from './router'
Vue.use(VueRouter);

import store from './store/store'


import '@/assets/style/reset.css'


import '@/assets/font/iconfont.css'


import axios from "./until/axios"
import VueAxios from 'vue-axios'
Vue.use(VueAxios, axios)

import Popup from "./until/ToastLoading"
Vue.prototype.$popup = Popup.install


const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
return originalPush.call(this, location).catch(err => err)
}



import XHeader from 'vux/src/components/x-header/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

Vue.component('x-button', XButton);
FastClick.attach(document.body)



router.beforeEach((to, from, next) => {

let isSeesion = localStorage.getItem("isSeesion");

if (to.path != '/login' && !isSeesion) {
next({
path: '/login'
});
} else {

if (localStorage.getItem("isSeesion") != "") {
next()
} else {
next({
path: '/login'
})
}
}
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
store,
render: h => h(App)
}).$mount('#app-box')


