import axios from '../until/axios'

export default {
    getLogin(mobile, password) {
        return axios
            .post(
                `/api/login/login`, { mobile, password });
    },
    getSessionID(accountId, pwd) {
        return axios
            .post(
                `/znms/AIS/audit/getToken`, { accountId, pwd });
    },
    openId() {
        let params = {
            pageNum: 1,
            pageSize: 10
        };
        return axios
            .post(
                `/api/calllog/list`, params);
    }
}


