//cookie
import Cookies from "js-cookie";
import model from "../API"
export default {
    //sessionID
    getSession({ commit }, { mobile, password }) {
        console.log(mobile, password)
        return new Promise((resolve, reject) => {
            console.log(mobile)
            model.getLogin(mobile, password)
                .then(data => {
                    resolve(data)
                }).catch(err => {
                    reject(err)
                })
        })
    },
    // getSession({ commit }, { accountId, pwd }) {
    //     return new Promise((resolve, reject) => {
    //         console.log(accountId)
    //         model.getSessionID(accountId, pwd)
    //             .then(data => {
    //                 commit("MsessionId", data)
    //                 resolve(data)
    //             }).catch(err => {
    //                 reject(err)
    //             })
    //     })
    // },
    GetUserInfo({ commit }) {
        return new Promise((resolve, reject) => {
            model.openId()
                .then(data => {
                    resolve(data)
                }).catch(err => {
                    reject(err)
                })
        })
    },

}


