export default {
  name: "Popup",
  data() {
    return {
      show: false,
      imgUrl: "",
      title: "",
      content: "",
      btnText: "",
    };
  },
  created() {},
  methods: {
    btnClick() {
      this.click();
      this.show = false;
    },
  },
};



