

import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

export default {
components: {
Tabbar,
TabbarItem,
},
data() {
return {
menuList: [
{
link: "/answeringRecord",
iconClass: "icon icon-answeringRecord",
des: "",
selected: "answeringRecord",
},
{
link: "/favorite",
iconClass: "icon icon-favorite",
des: "",
selected: "favorite",
},
{
link: "/secretarySetUp",
iconClass: "icon icon-secretarySetUp",
des: "",
selected: "secretarySetUp",
},
{
link: "/personalCustom",
iconClass: "icon icon-personalCustom",
des: "",
selected: "personalCustom",
},
],
currentSelect: "",
};
},
mounted() {
this.currentSelect = window.location.hash.split("/")[
window.location.hash.split("/").length - 1
];
},
};



