

import miHeader from "../common/header";


import until from "@/until/until";

import XInput from 'vux/src/components/x-input/index.vue'


import Cookies from "js-cookie";

import UserInfo from "./loginCommon";

import { mapState, mapActions, mapMutations } from "vuex";
export default {
components: {
miHeader,
XInput,
},
data() {
return {
title: "",
tableList: [],
FormInput: {
phone: "18280461633",
accesCode: "1234567b",
},
};
},
created() {
this.clearLocation()
},
mounted() {},
computed: {
...mapState(["wechartWH"]),
},
methods: {

...mapActions(["getSession", "GetUserInfo"]),
...mapMutations(["MopenId"]),
clearLocation() {
localStorage.removeItem("nowDate");
localStorage.removeItem("isSeesion");
},
getAuthCode() {

let loginParmas = {
mobile: this.FormInput.phone,
password: this.FormInput.accesCode,
};
let self = this;

UserInfo.getAuthCode(loginParmas, this.getSession, this.GetUserInfo).then(
function (res) {

self.$router.push({ path: "/answeringRecord" });
}
);
},
},
};



