import Vue from 'vue'
import Popup from '../components/common/Toast'
const PopupBox = Vue.extend(Popup)

Popup.install = function(data) {
    let instance = new PopupBox({
        data
    }).$mount()

    document.body.appendChild(instance.$el)

    Vue.nextTick(() => {
        instance.show = true
            // show show
    })
}
export default Popup


