import Router from 'vue-router'
//,
import login from '@/components/Login/login'
//
import mainPage from "@/components/mainPage";
//
import answeringRecord from "@/components/answeringRecord/answeringRecord";
//
import favorite from "@/components/favorite/favorite";
//
import secretarySetUp from "@/components/secretarySetUp/secretarySetUp";
//
import personalCustom from "@/components/personalCustomization/personalCustomization";
//
import help from "@/components/personalCustomization/help";
export default new Router({
    mode: "history",
    routes: [{
            path: '/login',
            name: 'login',
            component: login,
        },
        {
            path: '/',
            name: 'mainPage',
            redirect: '/answeringRecord',
            component: mainPage,
            children: [{
                    path: '/answeringRecord',
                    name: 'answeringRecord',
                    component: answeringRecord

                },
                {
                    path: '/favorite',
                    name: 'favorite',
                    component: favorite
                },
                {
                    path: '/secretarySetUp',
                    name: 'secretarySetUp',
                    component: secretarySetUp
                },
                {
                    path: '/personalCustom',
                    name: 'personalCustom',
                    component: personalCustom,
                },
            ]
        },

    ]
})


