  components: {
    miHeader,
  },
  data() {
    return {
      title: "",
    };
  },
  computed: {
    ...mapState(["isShowFooter"]),
  },
  created() {
    // this.$store.commit("handleChangeFooter", !this.isShowFooter);
  },
  methods: {
    ...mapMutations(["handleChangeFooter"]),
    handelback() {
      // this.$store.commit("handleChangeFooter", !this.isShowFooter);
    },
  },
};



